.background {
  background-color: #121212;
}

.wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 8px;

  width: 100%;
  max-width: 1110px;
  min-height: 64px;
  margin: 0 auto;
}

.text {
  font-family: Spartan, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;

  display: flex;
  align-items: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #FFFFFF;
}
.form {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  width: 100%;
  max-width: 520px;
  height: 100%;
  min-height: 56px;
  /*min-height: 32px;*/
  position: relative;
}

.inputEmail:-webkit-autofill,
.inputEmail:-webkit-autofill:hover,
.inputEmail:-webkit-autofill:focus,
.inputEmail:-webkit-autofill:active  {
  -webkit-box-shadow: 0 0 0 150px rgb(49, 48, 48) inset !important;
  -webkit-text-fill-color: #FFFFFF;
  /*border: 1px solid rgba(170, 32, 97, 0.95);*/
  /*-webkit-text-fill-color: red !important;*/
}
.formError {
  position: absolute;
  top: 43px;
  color: #E91E63;
}
.successSubmitText,
.errorSubmitText {
  position: absolute;
  top: 43px;
}
.successSubmitText {
  color: #89bb36;
}
.errorSubmitText {
  color: red;
}
.inputEmailWrapper {
  height: 100%;
  width: 100%;
}
.inputEmail {
  height: 100%;
  min-height: 32px;
  max-width: 400px;
  width: 100%;
  padding-top: 3px;
  padding-left: 8px;

  background-color: rgba(64, 64, 64, 0.71);
  outline: none;
  border: 0;
  cursor: pointer;
  transition: box-shadow 0.22s ease-in-out;

  font-family: Spartan, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  color: #FFFFFF;
  text-shadow: 1px 1px 4px rgba(0, 255, 247, 0.97);
}
.inputEmailError {
  box-shadow: 0 0 3px rgba(239, 123, 186, 0.93) !important;
}
.inputEmail:hover {
  box-shadow: 0 0 3px rgb(201, 197, 229);
}
.inputEmail:focus {
  cursor: text;
  box-shadow: 0 0 4px rgb(226, 224, 245);
}
.inputEmail::placeholder {
  font-family: Spartan, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  padding-left: 14px;
  color: #FFFFFF;
  opacity: 0.4;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.submit {
  max-width: 108px;
  width: 100%;
  /*height: 100%;*/
  height: 32px;
  padding-top: 2px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #121212;
  border: 1px solid #FFFFFF;
  cursor: pointer;
  transition: 0.15s ease-in-out;

  font-family: Spartan, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #FFFFFF;
  opacity: 0.9;
}
.submit:hover {
  box-shadow: 0 0 5px rgb(201, 197, 229);
  text-shadow: 0 0 1px rgb(201, 197, 229);
}
.submit:focus {
  box-shadow: 0 0 5px rgb(226, 224, 245);
  text-shadow: 0 0 1px rgb(226, 224, 245);
}
.submit div div {
  width: 24px;
  height: 24px;
}
.messengers {
  width: 100%;
  max-width: 108px;
  height: 18px;
}

@media screen and (max-width: 633px) {
  .wrapper {
    justify-content: center;
  }
}